html {
  box-sizing: border-box;
  font-size: 52.5%;
  /** Reset para REMS  65,5% = 10px de 16px**/
  scroll-behavior: smooth;
  
}

body {
  background-color:rgba(37, 184, 172, 0.623);
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
  color: black;
}
.sc {
  overflow-y: hidden;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}


h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 1.6rem;
}

a:hover {
  list-style: none;
  text-decoration: none;
  cursor: pointer;
}
.main_nav {
  ul {
    display: flex;
    flex-direction: row;
    max-width: 1200px;
    justify-content: flex-end;
    li {
      list-style: none;
      margin: 1rem;
    }
  }
}
